import Bridge from './ios.js';
import Vue from 'vue';
// 判断是否是安卓或ios
let u = navigator.userAgent;

const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);

// 获取电话
const getPhone = () => {
    if (isiOS) {
        Bridge.callhandler('callNativeGetPhone', {}, res => {
                Vue.prototype.tokus.phone = res
            })
            // const callNativeGetPhone = () => {  
            // }
    } else {
        // window.android.callNativeGetPhone();
    }
}

// 设置状态栏颜色值    
const setTitleColor = style => {
        if (isiOS) {
            Bridge.callhandler('callNativeSetTitleColor', { style }, res => {})
                // window.webkit.messageHandlers.callNativeTelPhone.postMessage({ phone })

        } else {
            // console.log(phone)
            // window.android.callNativeSetTitleColor(phone);
        }
    }
    // 拨打电话
const telPhone = phone => {
        if (isiOS) {
            Bridge.callhandler('callNativeTelPhone', { phone }, res => {})
                // window.webkit.messageHandlers.callNativeTelPhone.postMessage({ phone })
        } else {
            console.log(phone)
            window.android.callNativeTelPhone(phone);
        }
    }
    // 返回上一页
const goBack = () => {
        if (isiOS) {
            Bridge.callhandler('callNativeGoBack', {}, res => {})
                // window.webkit.messageHandlers.callNativeGoBack.postMessage({ phone })
        } else {
            console.log('返回')
            window.android.callNativeGoBack();
        }
    }
    // 获取链接传值
const getTokus = (that) => {
    // if (process.env.NODE_ENV !== 'development') {

    // }
    // console.log('222')
    if (isiOS) {
        let tokus = that.$route.params.tokus
        console.log(tokus)
        Vue.prototype.tokus['token'] = tokus
        console.log(tokus)
    } else {
        console.log('an')
        let tokus = JSON.parse(that.$route.params.tokus)
        if (typeof tokus === 'object') {
            Vue.prototype.tokus = tokus
        }
    }
    // let tokus = JSON.parse(that.$route.params.tokus)
    // console.log('token')
    // if (typeof tokus === 'object') {
    //     console.dir(tokus)
    //     Vue.prototype.tokus = tokus
    //     console.log(tokus)
    // }
}
export { isiOS, telPhone, goBack, getTokus, getPhone, setTitleColor }


