import CryptoJS from 'crypto-js'
import md5 from 'js-md5'
const Todate = (num) => { //Fri Oct 31 18:00:00 UTC+0800 2008
    let date;
    if (num) {
        date = new Date(num);
    } else {
        date = new Date();
    }
    console.log(date)
    let Month = (date.getMonth() + 1) >= 10 ? (date.getMonth() + 1) : '0' + (date.getMonth() + 1)
    let day = date.getDate() >= 10 ? date.getDate() : '0' + date.getDate()
    let date_value = date.getFullYear() + '-' + Month + '-' + day;
    return date_value;
}
const getRequest = () => {
    let url = window.location.search; //获取url中"?"符后的字串
    let theRequest = new Object();
    if (url.indexOf("?") != -1) {
        let str = url.substr(1);
        strs = str.split("&");
        for (let i = 0; i < strs.length; i++) {

            theRequest[strs[i].split("=")[0]] = decodeURI(strs[i].split("=")[1]);

        }
    }
    return theRequest;
}
const encrypt = (plaintText) => {
    let options = {
            iv: CryptoJS.enc.Utf8.parse("1234567890123456"),
            mode: CryptoJS.mode.CBC,
            padding: CryptoJS.pad.Pkcs7
        }
        // const key = 'abcdefghijklmnop';
    const key = CryptoJS.enc.Utf8.parse("abcdefghijklmnop"); //十六位十六进制数作为密钥
    // let srcs = word;
    let encrypted = CryptoJS.AES.encrypt(plaintText, key, options);
    return encrypted.toString();
}

