import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const order = r => require.ensure([], () => r(require('../views/order/')), 'order'); //订单管理
const bill = r => require.ensure([], () => r(require('../views/bill/')), 'bill'); //账单
const withdrawal = r => require.ensure([], () => r(require('../views/withdrawal/')), 'withdrawal'); //提现
const changePassword = r => require.ensure([], () => r(require('../views/changePassword/')), 'changePassword'); //修改密码
const setPassword = r => require.ensure([], () => r(require('../views/setPassword/')), 'setPassword'); //设置密码


export default new Router({
    routes: [
        //二级路由,地址为空时跳转地址
        // {
        //     path: '/',
        //     redirect: '/bill'
        // },
        //主页面
        {
            path: '/bill/:tokus',
            component: bill,
            name: 'bill',
            meta: {
                title: '账单'
            },
        },
        {
            path: '/order/:tokus',
            component: order,
            name: 'order',
            meta: {
                title: '订单管理'
            },
        },
        {
            path: `/withdrawal/:tokus`,
            component: withdrawal,
            name: 'withdrawal',
            meta: {
                title: '提现'
            },
        },
        {
            path: `/changePassword/:id`,
            component: changePassword,
            name: 'changePassword',
            meta: {
                title: '修改密码'
            },
        },
        {
            path: `/setPassword/:temp_token`,
            component: setPassword,
            name: 'setPassword',
            meta: {
                title: '设置密码'
            },
        }
    ]
})


